:root {
  --green_color: #3dcfb6;
  --black_color: #1a1a1a;
  --white_color: #ededed;
  --brand-color: hsl(46, 100%, 50%);
  --black: hsl(0, 0%, 0%);
  --white: hsl(0, 0%, 100%);
  --semiwhite_color: rgba(237, 237, 237, 0.5);
  --primary-font: red;
  --secundary_font: red;
}

.button--primary {
  padding: 8px;
  margin: 0 auto;
  background: var(--black_color);
  border: 1px solid var(--green_color);
  color: var(--green_color);
  box-shadow: -4px -4px 10px rgba(61, 207, 182, 0.5),
    4px 4px 10px rgba(0, 0, 0, 0.8);
  border-radius: 15px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease, border 0.3s ease,
    box-shadow 0.3s ease, transform 0.3s ease;
}

.button--primary:hover {
  color: var(--black_color);
  background-color: var(--green_color);
  border: 1px solid var(--black_color);
  box-shadow: -4px -4px 10px rgba(61, 207, 182, 0.5),
    4px 4px 10px rgba(61, 207, 182, 0.5);
  transform: scale(1.05);
}

.button--secundary {
  color: var(--black_color);
  background-color: var(--green_color);
  border: 1px solid var(--black_color);
  box-shadow: -4px -4px 10px rgba(61, 207, 182, 0.5),
    4px 4px 10px rgba(61, 207, 182, 0.5);
  padding: 10px 20px;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease, border 0.3s ease,
    box-shadow 0.3s ease, transform 0.3s ease;
}

.button--secundary:hover {
  transform: scale(1.05);
}

body {
  margin: 0;
  font-family: "Prosto One", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.line-text {
  display: flex;
  align-items: center;
  text-align: center;
}

.line-text::before,
.line-text::after {
  content: "";
  flex: 1;
  border-bottom: 2px solid var(--green_color);
  margin: 0 10px;
}

.global-container {
  margin-bottom: 50px;
}

